.services {
    width: 100%;
}

.services .container {
    width: 1200px;
    margin: 0 auto;
}

.services h1 {
    font-size: 3rem;
    text-align: center;
    padding-top: 7rem;
}

.services h2 {
    display: flex;
    font-size: 1.4rem;
    color: white;
    text-align: center;
    text-shadow: 2px 2px 4px rgba(0,0,0,0.7);
    background-color: rgba(0,0,0,0.5);
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    padding: 1rem;
    height: 20%;
    align-items: center;
    justify-content: center;
}

.services p {
    display: flex;
    color: white;
    text-align: center;
    text-shadow: 2px 2px 4px rgba(0,0,0,0.7);
    background-color: rgba(0,0,0,0.5);
    /* border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px; */
    padding: 1.5rem;
    height: 30%;
    align-items: center;
    justify-content: center;
}

.services .container .content {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    padding: 2rem 0 0 0;
    max-width: 1200px;
}

.services .card {
    width: 100%;
    height: 400px;
    position: relative;
    transition: 0.3s all;
  }

  .services .card:hover {
    width: 120%;
  }

  .services .card img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 20px;
  }

  .services .card .details {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: absolute;
    top: 0;
    height: 100%;
    width: 100%;
    transition: 0.3s all;
  }

@media screen and (max-width: 900px) {
    .services .container {
        width: 100%;
        padding: 0 1rem;
    }

    .services .container .content {
        flex-direction: column;
        -webkit-flex-direction: column;
    }

    .services .card {
        max-width: 100%;
    }
}









