.contact {
    color: #fff;
    width: 100%;
    position: relative;
    background: url('./images/product4.jpg') no-repeat center/cover;
}

.contact .background {
    background-color: rgba(0, 0, 0, 0.4);
    width: 100%;
    height: 100%;
    padding-top: 6rem;
    display: flex;
    justify-content: center;
}

.contact .content {
    max-width: 1200px;
}

.contact h1 {
    font-size: 3rem;
    text-align: center;
    margin-bottom: 0.5rem;
}

.contact .content p {
    margin-top: 0.3rem;
    font-size: 1.5rem;
    font-weight: 600;
    text-align: center;
}

.contact .content p:first-of-type {
    margin-top: 2rem;
}

.contact form {
    background-color: rgba(255, 255, 255, 0.2);
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 2rem 0.5rem;
    padding: 1.5rem 0 0.5rem 0;
    max-width: 1200px;
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.5);
}

.contact form label {
    font-weight: bold;
    font-size: 1.3rem;
    margin-bottom: 0.6rem;
}

form input,
form select,
form textarea {
    outline: none;
    padding: 10px 10px;
    border-radius: 20px;
    width: 80%;
    background-color: #eee;
    color: black;
    border: 1.5px solid #333;
    text-align: center;
    margin-bottom: 1rem;
    transition: all 0.3s;
}

form input:focus,
form select:focus,
form textarea:focus {
    background-color: #fff;
    border: 1.5px solid #111;
    box-shadow: 3px 3px 20px #222;
}

form select {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    background-image: url(./images/arrow.png);
    background-size: 10px;
    background-repeat: no-repeat;
    background-position: right 10px center;
}

form textarea {
    min-height: 100px;
    resize: none;
    font-family: sans-serif;
}

form input:disabled {
    cursor: default;
    background-color: gray;
}

@media screen and (max-width:900px) {

    .contact .content p {
        padding: 0.2rem 2rem;
        font-size: 1.1rem;
    }

}